import Head from "next/head";
import Navbar from "../components/Navbar";

const Layout = props => {
	return (
		<div>
			<Head>
				<title>BitzPrice</title>
				<link
					rel="stylesheet"
					href="https://stackpath.bootstrapcdn.com/bootswatch/4.4.1/lux/bootstrap.min.css"
				/>
			</Head>
			<Navbar />
			{props.children}
		</div>
	);
};

export default Layout;
